import React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import { Grid } from '@mui/material';
import DualTable from '../PortfolioTable/dualTable';
import bnb from '../../../assets/img/bnb.png';
import { MdArrowForwardIos } from 'react-icons/md';
const columns = [{ id: 'coin', label: 'Coin', minWidth: 170 }];
export default function DualList({ dual, val }) {
  const [coinsState, setCoinsState] = React.useState('BNB');
  const portFolioAsset = dual.filter((s) => s.coin === coinsState);
  const Coins = [];
  for (let i = 0; i < val.length; i++) {
    Coins.push(val[i].coin);
  }
  const unique = [...new Set(Coins)];
  const CoinHandler = (coin) => {
    setCoinsState(coin);
  };
  return (
    <Grid container>
      <Grid item xl={4} lg={4} md={4} className="w-full">
        <Grid
          item
          xl={12}
          className="border-b border-inherit xl:p-7 lg:p-7"></Grid>
        <div sx={{ width: '100%', overflow: 'hidden' }}>
          <TableContainer
            sx={{
              maxHeight: 352
            }}>
            <Table stickyHeader aria-label="sticky table">
              <TableBody>
                {unique.map((s) => {
                  return (
                    <TableRow hover tabIndex={-1}>
                      {columns.map((column) => {
                        const value = s;
                        return (
                          <TableCell
                            key={column.id}
                            align={column.align}
                            className={`border-r border-inherit ${
                              coinsState === value && 'coins-block-color'
                            }`}
                            onClick={() => CoinHandler(value)}>
                            <Grid container className="p-2">
                              <Grid item xl={3} md={3} lg={3} className="mr-2">
                                <img
                                  src={bnb}
                                  alt=""
                                  className="portfolio-img"
                                />
                              </Grid>
                              <Grid item xl={3} md={3} lg={3}>
                                <div className="text-xs opacity-75 font-semibold mt-3 ml-2">
                                  {column.format && typeof value === 'number'
                                    ? column.format(value)
                                    : value}
                                </div>
                              </Grid>
                              <Grid item xl={2} md={2} lg={2}></Grid>
                              <Grid item xl={3} md={3} lg={3}>
                                <div
                                  className="mt-1  flex justify-end portfolio-arrow-color
                                  ">
                                  <MdArrowForwardIos className="ml-2  mt-1 arrow" />
                                </div>
                              </Grid>
                            </Grid>
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </Grid>
      <Grid item xl={8} lg={8} md={8} className="w-full">
        <DualTable dual={portFolioAsset} />
      </Grid>
    </Grid>
  );
}
