import React, { useState, useEffect } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Donutchart from '../Charts/donutChart';
import { fetchCoins } from '../../../hooks/useFetchCoin';
import Legend from '../Legends/legend';
import CustomizedMenus from '../PoolDropDown';
import { fourArrays } from '../../admin/Dashboard/helper';
import { Grid } from '@mui/material';
function FundsCard({ title, children: icon }) {
  const [coins, setCoins] = useState({ BNB: [], ETH: [], BTC: [], USDC: [] });
  const [chainType, setChainType] = useState('All Chains');
  useEffect(() => {
    (async () => {
      const data = await fetchCoins();
      setCoins(fourArrays(data.data));
    })();
  }, []);
  return (
    <Card className="bg-card-container border border-inherit dark:shadow-sm">
      <div className="space border border-inherit p-3 ">
        <p className="m-0 text-base font-medium ">{title}</p>
        <span className="text-sm text-purple-600">
          <CustomizedMenus chainType={chainType} setChainType={setChainType} />
        </span>
      </div>
      <CardContent>
        <Grid container className="flex justify-center">
          <Grid item md={6} xl={6} lg={6}>
            <Donutchart coins={coins} />
          </Grid>
          <Grid item md={6} xl={6} lg={6}>
            <div className=" grid gap-y-0.5 justify-center align-center mr-3">
              <Legend name="BNB" val={coins.BNB} color="#7A6DE2" />
              <Legend name="ETH" val={coins.ETH} color="#FF9F40" />
              <Legend name="BTC" val={coins.BTC} color="#FF9F40" />
              <Legend name="USDC" val={coins.USDC} color="#A9A1EC" />
            </div>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}
export default FundsCard;
