import React, { useState, useEffect } from 'react';
import Card from '@mui/material/Card';
import PortfolioTab from '../Pannel/tabs';
import { fetchAsset } from '../../../hooks/useFetchCoin';
import _ from 'lodash';
function PortfolioCard({ title, children: icon }) {
  const [asset, setAsset] = useState([]);
  const [options, setOptions] = useState('ALL');
  const [portfolArr, setPortFolArr] = useState([]);
  const filter = (opt) => {
    setOptions(opt);
    if (portfolArr) {
      const ByName = _.groupBy(portfolArr, 'name');
      switch (opt) {
        case 'ALL':
          setAsset(portfolArr);
          break;
        case 'BSC':
          setAsset(ByName.BSC);
          break;
        case 'MATIC':
          setAsset(ByName.MATIC);
          break;
        case 'AVAX':
          setAsset(ByName.AVAX);
          break;
        default:
          break;
      }
    }
  };
  useEffect(() => {
    (async () => {
      const data = await fetchAsset();
      setAsset(data.data);
      setPortFolArr(data.data);
    })();
  }, []);
  return (
    <Card className="bg-card-container border border-inherit dark:shadow-sm">
      <div className="xl:grid xl:grid-cols-5 xl:divide-x ">
        <div className=" border-r-0.5 border-inherit p-3">
          <p className="m-0 mx-2 text-base font-medium my-2">{title}</p>
        </div>
        <div className="flex p-3 top-button-border">
          <button
            className={`cbtn-search items-center mr-2 ${
              options === 'ALL' && 'btn-hover'
            }`}
            onClick={() => filter('ALL')}>
            <span className="ml-1">All</span>
          </button>
          <button
            className={`cbtn-search items-center mr-2 ${
              options === 'BSC' && 'btn-hover'
            }`}
            onClick={() => filter('BSC')}>
            <span className="ml-1">BSC</span>
          </button>
          <button
            className={`cbtn-search items-center mr-2 ${
              options === 'MATIC' && 'btn-hover'
            }`}
            onClick={() => filter('MATIC')}>
            <span className="ml-1">MATIC</span>
          </button>
          <button
            className={`cbtn-search items-center mr-2 ${
              options === 'AVAX' && 'btn-hover'
            }`}
            onClick={() => filter('AVAX')}>
            <span className="ml-1">AVAX</span>
          </button>
        </div>
      </div>
      <div className="border-t border-inherit">
        <PortfolioTab asset={asset} val={portfolArr} />
      </div>
    </Card>
  );
}

export default PortfolioCard;
