import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import { TabList, TabPanel } from '@mui/lab';
import Linechart from '../Charts/lineChart';
import { fetchPrices } from '../../../hooks/useFetchCoin';
import { Chartfilter } from '../../filters';
import { IoIosArrowDown } from 'react-icons/io';
export default function LabTabs() {
  const [opt, setOpt] = useState('w');
  const [value, setValue] = useState('1');
  const [prices, setPrices] = useState([]);
  const [filterArr, setFilterArr] = useState([]);
  useEffect(() => {
    (async () => {
      const data = await fetchPrices();
      setPrices(data.data);
    })();
  }, []);
  useEffect(() => {
    ApplyFilter(opt);
  }, [opt, prices]);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const ApplyFilter = (opt) => {
    setOpt(opt);
    if (prices) {
      const Arr = Chartfilter(prices, opt);
      setFilterArr(Arr);
    }
  };
  return (
    <>
      <div className="border border-white rounded-2xl w-full">
        <Box sx={{ width: '100%', typography: 'body1' }}>
          <TabContext value={value}>
            <Box
              sx={{
                borderBottom: 1,
                borderColor: 'divider-white'
              }}
              className="text-white xl:flex justify-between">
              <TabList
                onChange={handleChange}
                aria-label="lab API tabs example"
                textColor="white">
                <Tab
                  label="1 Week"
                  value="1"
                  onClick={() => ApplyFilter('w')}
                />
                <Tab
                  label="1 Month"
                  value="2"
                  onClick={() => ApplyFilter('month')}
                />
                <Tab
                  label="3 Month"
                  value="3"
                  onClick={() => ApplyFilter('3m')}
                />
              </TabList>
              <div className="xl:ml-14 xl:border-l border-white">
                <div className=" flex items-center down text-sm pl-7 pr-7 pb-3 pt-3 labTab-top">
                  <text className="mr-2 ml-4">All Users</text>
                  <IoIosArrowDown className="fSize mt-1" />
                </div>
              </div>
            </Box>
            <TabPanel value="1">
              <Linechart prices={filterArr} />
            </TabPanel>
            <TabPanel value="2">
              <Linechart prices={filterArr} />
            </TabPanel>
            <TabPanel value="3">
              <Linechart prices={filterArr} />
            </TabPanel>
          </TabContext>
          <div className="border-t border-white h-12">
            <div className="flex justify-end down m-4 text-xs">
              <text className="mr-1">Download</text>
              <IoIosArrowDown className="fdSize" />
            </div>
          </div>
        </Box>
      </div>
    </>
  );
}
