import React from 'react';
import Circle from '@mui/icons-material/Circle';
const Legend = ({ name, val, color }) => {
  return (
    <div className="flex justify-center p-3">
      {val &&
        val.map((v) => (
          <div className="flex-row">
            <div className="flex">
              <Circle
                sx={{ fontSize: '10px', backgroundColor: { color } }}
                className="m-2"
              />
              <div className="text-xs mr-2">{name}</div>
            </div>
            <div className="text-xs ml-7">{v}</div>
            <div className="mr-2"></div>
          </div>
        ))}
    </div>
  );
};

export default Legend;
