import React from 'react';
import Chart from 'react-apexcharts';
import dayjs from 'dayjs';
function lineChart({ prices }) {
  const date = [];
  const val = [];
  if (prices) {
    for (let i = 0; i < prices.length; i++) {
      val.push(prices[i].value);
      date.push(dayjs(prices[i].date).format('DD-MM'));
    }
  }
  return (
    <React.Fragment>
      <div className="container-fluid mt-3 mb-3">
        <Chart
          type="line"
          height={185}
          series={[
            {
              data: val
            }
          ]}
          options={{
            labels: date,

            plotOptions: {
              pie: {
                donut: {
                  labels: {
                    show: false
                  }
                }
              }
            },
            dataLabels: {
              enabled: false
            },
            xaxis: {
              labels: {
                style: {
                  colors: '#fff'
                }
              }
            },
            yaxis: {
              labels: {
                style: {
                  colors: '#fff'
                }
              }
            }
          }}
        />
      </div>
    </React.Fragment>
  );
}
export default lineChart;
