import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';

export default function PoolTab({ time, setTime }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box>
      <Tabs
        value={value}
        onChange={handleChange}
        variant="scrollable"
        TabIndicatorProps={{
          style: {
            display: 'none'
          }
        }}
        scrollButtons={false}
        aria-label="scrollable prevent tabs example"
        className="border border-inherit rounded-3xl pool-bar-color w-full">
        <Tab
          label="1H"
          className="pool-element pool-text-size"
          onClick={() => setTime('h')}></Tab>
        <Tab
          label="1D"
          className="pool-element pool-text-size"
          onClick={() => setTime('d')}
        />
        <Tab
          label="1W"
          className="pool-element pool-text-size"
          onClick={() => setTime('w')}
        />
        <Tab
          label="1M"
          className="pool-element pool-text-size"
          onClick={() => setTime('month')}
        />
        <Tab
          label="1Y"
          className="pool-element pool-text-size"
          onClick={() => setTime('y')}
        />
        <Tab
          label="All"
          className="pool-element pool-text-size"
          onClick={() => setTime('a')}
        />
      </Tabs>
    </Box>
  );
}
