import { useCallback } from 'react';
export const fourArrays = (coins) => {
  const BNBarr = [];
  const BTCarr = [];
  const ETHarr = [];
  const USDCarr = [];
  coins.map((c) => {
    if (c.name === 'BNB') {
      BNBarr.push(c.value);
    } else if (c.name === 'ETH') {
      ETHarr.push(c.value);
    } else if (c.name === 'BTC') {
      BTCarr.push(c.value);
    } else if (c.name === 'USDC') {
      USDCarr.push(c.value);
    }
  });
  return { BNB: BNBarr, BTC: BTCarr, ETH: ETHarr, USDC: USDCarr };
};
export const sumCoins = (coins) => {
  let sumBNB = 0;
  let sumETH = 0;
  let sumBTC = 0;
  let sumUSDC = 0;
  coins.BNB.map((b) => {
    sumBNB = sumBNB + b;
  });
  coins.ETH.map((b) => {
    sumETH = sumETH + b;
  });
  coins.BTC.map((b) => {
    sumBTC = sumBTC + b;
  });
  coins.USDC.map((b) => {
    sumUSDC = sumUSDC + b;
  });
  return { BNB: sumBNB, ETH: sumETH, BTC: sumBTC, USDC: sumUSDC };
};
