import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import CoinList from '../Lists/singleList';
import DualList from '../Lists/dualList';
export default function PortfolioTab({ asset, val }) {
  const [value, setValue] = useState('1');
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const singleAsset = asset.filter((a) => a.type === 'single');
  const dualAsset = asset.filter((a) => a.type === 'dual');
  return (
    <Box sx={{ width: '100%', typography: 'body1' }}>
      <TabContext value={value}>
        <Box
          sx={{
            borderBottom: 1,
            borderColor: 'divider'
          }}>
          <TabList onChange={handleChange} className="">
            <Tab
              label="Single Asset"
              value="1"
              sx={{ textTransform: 'capitalize', color: 'black' }}
              className="single-tab-color"
            />
            <Tab
              label="Dual Asset"
              value="2"
              sx={{ textTransform: 'capitalize', color: 'black' }}
              className="single-tab-color"
            />
          </TabList>
        </Box>
        <TabPanel value="1" className="portfolio-table-pading single-tab-color">
          <CoinList single={singleAsset} val={val} />
        </TabPanel>
        <TabPanel value="2" className="portfolio-table-pading">
          <DualList dual={dualAsset} val={val} />
        </TabPanel>
      </TabContext>
    </Box>
  );
}
