import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Divider from '@mui/material/Divider';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right'
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color:
      theme.palette.mode === 'light'
        ? 'rgb(55, 65, 81)'
        : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0'
    },
    '& .MuiMenuItem-root': {
      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5)
      },
      '&:active': {
        backgroundColor: alpha(
          theme.palette.primary.main,
          theme.palette.action.selectedOpacity
        )
      }
    }
  }
}));

export default function CustomizedMenus({ chainType, setChainType }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (chain) => {
    setChainType(chain);
    setAnchorEl(null);
  };
  const sideClose = () => {
    setAnchorEl(null);
  };
  return (
    <div>
      <div className="text-primary">
        <Button
          id="demo-customized-button"
          variant="false"
          onClick={handleClick}
          disableRipple="true"
          endIcon={<KeyboardArrowDownIcon />}>
          {chainType}
        </Button>
      </div>
      <StyledMenu
        id="demo-customized-menu"
        MenuListProps={{
          'aria-labelledby': 'demo-customized-button'
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={sideClose}>
        <MenuItem onClick={() => handleClose('All Chains')} disableRipple>
          All Chains
        </MenuItem>
        <Divider />
        <MenuItem onClick={() => handleClose('BSC')} disableRipple>
          BSC
        </MenuItem>
        <Divider />
        <MenuItem onClick={() => handleClose('MATIC')} disableRipple>
          MATIC
        </MenuItem>
        <Divider />
        <MenuItem onClick={() => handleClose('AVAX')} disableRipple>
          AVAX
        </MenuItem>
      </StyledMenu>
    </div>
  );
}
