import _ from 'lodash';
import dayjs from 'dayjs';
export const filterByType = (arr, type) => {
  const data = _.groupBy(arr, 'type');
  if (type === 'all') {
    return arr;
  } else if (type === 'single') {
    return data.single;
  } else if (type === 'dual') {
    return data.dual;
  } else {
    return;
  }
};
export const filterByTime = (arr, time) => {
  const endTime = dayjs().format('YYYY-MM-DD HH:mm:ss');
  if (time === 'a') {
    return arr;
  } else {
    let startTime = dayjs(endTime)
      .subtract(1, time)
      .format('YYYY-MM-DD HH:mm:ss');
    const data = arr?.filter(
      (a) =>
        dayjs(a.time).isAfter(dayjs(startTime)) &&
        dayjs(a.time).isBefore(dayjs(endTime))
    );
    return data;
  }
};
export const Chartfilter = (arr, time) => {
  const endTime = dayjs().format('YYYY-MM-DD HH:mm:ss');
  if (time === '3m') {
    let startTime = dayjs(endTime)
      .subtract(3, 'month')
      .format('YYYY-MM-DD HH:mm:ss');
    const data = arr.filter(
      (a) =>
        dayjs(a.date).isAfter(dayjs(startTime)) &&
        dayjs(a.date).isBefore(dayjs(endTime))
    );
    return data;
  } else {
    let startTime = dayjs(endTime)
      .subtract(1, time)
      .format('YYYY-MM-DD HH:mm:ss');
    const data = arr?.filter(
      (a) =>
        dayjs(a.date).isAfter(dayjs(startTime)) &&
        dayjs(a.date).isBefore(dayjs(endTime))
    );
    return data;
  }
};
