import React, { useEffect, useState } from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer } from 'recharts';
import { sumCoins } from '../../admin/Dashboard/helper';
export default function Donutchart({ coins }) {
  const COLORS = ['#7A6DE2', '#FF9F40', '#F4BE37', '#A9A1EC'];
  const [coinsArr, setCoinsArr] = useState({
    BNB: 0,
    ETH: 0,
    BTC: 0,
    USDC: 0
  });
  useEffect(() => {
    setCoinsArr(sumCoins(coins));
  }, [coins]);

  const data = [
    { name: 'BNB', value: coinsArr.BNB },
    { name: 'ETH', value: coinsArr.ETH },
    { name: 'BTC', value: coinsArr.BTC },
    { name: 'USDC', value: coinsArr.USDC }
  ];
  return (
    <div style={{ width: '100%', height: 300 }} className="flex">
      <ResponsiveContainer>
        <PieChart>
          <Pie
            data={data}
            innerRadius={70}
            outerRadius={110}
            fill="#8884d8"
            legendType="circle"
            paddingAngle={1}>
            {data.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}
