import React, { useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { Grid } from '@mui/material';
import PoolTab from '../poolBar/timeTab';
import CustomizedMenus from '../PoolDropDown';
import { fetchAsset } from '../../../hooks/useFetchCoin';
import { filterByType, filterByTime } from '../../filters';
import PoolTable from '../PoolTable/table';
import _ from 'lodash';
import { MdArrowForwardIos } from 'react-icons/md';
import { IoIosArrowDown } from 'react-icons/io';
import { IoIosArrowUp } from 'react-icons/io';
function PoolCard({ title, value, children: icon }) {
  const [data, setData] = useState([]);
  const [assets, setAsset] = useState([]);
  const [chainType, setChainType] = useState('All Chains');
  const [assetType, setAssetType] = useState('all');
  const [time, setTime] = useState('h');
  const [order, setOrder] = useState('high');
  useEffect(() => {
    (async () => {
      const res = await fetchAsset();
      setData(res.data);
      setAsset(res.data);
    })();
  }, []);
  useEffect(() => {
    (async () => {
      if (chainType === 'All Chains') {
        const allChain = await filterByType(data, assetType);
        const arr = filterByTime(allChain, time);
        order === 'high'
          ? setAsset(_.orderBy(arr, 'reward', 'desc'), 'high to low')
          : setAsset(_.orderBy(arr, 'reward', 'asc'), 'low to high');
      } else {
        const res = data.filter((a) => a.name === chainType);
        const filter = await filterByType(res, assetType);
        const arr = await filterByTime(filter, time);
        order === 'high'
          ? setAsset(_.orderBy(arr, 'reward', 'desc'))
          : setAsset(_.orderBy(arr, 'reward', 'asc'));
      }
    })();
  }, [chainType, assetType, time, order, data]);
  return (
    <Card className="bg-card-container border border-inherit dark:shadow-sm">
      <div className=" flex space border border-inherit p-3 ">
        <p className="m-0 mx-2 text-base font-medium ">{title}</p>
        <div className="text-sm">
          <CustomizedMenus chainType={chainType} setChainType={setChainType} />
        </div>
      </div>
      <Grid container className="heightAuto pool-container ">
        {/* left Grid */}
        <Grid
          item
          md={4}
          className="border-r border-inherit pool-asset-background w-full">
          <Grid container className="bg-white">
            {/* empty side div */}
            <Grid
              item
              xl={12}
              className="border-b border-inherit xl:py-10 lg:py-10 text-center"
              sx={{ width: '100%', height: '100%' }}></Grid>
            <Grid
              item
              xl={12}
              className={`border-b border-inherit py-9 text-center  text-sm pool-asset-div ${
                assetType === 'all'
                  ? ' bg-white text-primary'
                  : ' pool-asset-background '
              }
              `}
              onClick={() => setAssetType('all')}>
              <Grid container className="ml-3 ">
                <Grid item md={6}>
                  <text className=" font-semibold ">All Assets</text>
                </Grid>
                <Grid item md={6}>
                  <MdArrowForwardIos className="ml-2 arrow" />
                </Grid>
              </Grid>
            </Grid>
            <Grid
              item
              xl={12}
              className={`border-b border-inherit py-9 text-center  text-sm pool-asset-div ${
                assetType === 'single'
                  ? ' bg-white text-primary'
                  : ' pool-asset-background '
              }
              `}
              onClick={() => setAssetType('single')}>
              <Grid container className="ml-3">
                <Grid item md={6}>
                  <text className=" font-semibold">Single Assets</text>
                </Grid>
                <Grid item md={6}>
                  <MdArrowForwardIos className="ml-2 arrow" />
                </Grid>
              </Grid>
            </Grid>
            <Grid
              item
              xl={12}
              className={`border-b border-inherit py-9 text-center  text-sm pool-asset-div ${
                assetType === 'dual'
                  ? ' bg-white text-primary'
                  : ' pool-asset-background '
              }
              `}
              onClick={() => setAssetType('dual')}>
              <Grid container className="ml-3 ">
                <Grid item md={6}>
                  <text className="font-semibold">Dual Assets</text>
                </Grid>
                <Grid item md={6}>
                  <MdArrowForwardIos className="ml-2 arrow" />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {/* Right Grid */}
        <Grid item md={8} className="w-full">
          {/* tab container */}
          <Grid container>
            <Grid
              item
              xl={12}
              className="border-b border-inherit py-4 text-center m-0 px-3"
              sx={{ width: '100%', height: '100%' }}>
              <PoolTab time={time} setTime={setTime} />
            </Grid>
          </Grid>
          {/* tabel of data */}
          <Grid container>
            <Grid
              item
              md={8}
              className="text-center m-0 px-3"
              sx={{ width: '100%', height: '100%' }}>
              <PoolTable single={assets} />
            </Grid>
            {order === 'high' ? (
              <Grid item md={4} onClick={() => setOrder('low')}>
                <div className=" flex justify-end text-center m-0 px-3 size-nine mt-5 text-primary font-semibold h-10">
                  <text className="mr-1">HIGH TO LOW</text>
                  <IoIosArrowUp className="below" />
                </div>
              </Grid>
            ) : (
              <Grid
                item
                md={4}
                onClick={() => {
                  setOrder('high');
                }}>
                <div className=" flex justify-end text-center m-0 px-3 size-nine mt-5 text-primary font-semibold h-10">
                  <text className="mr-1">LOW TO HIGH</text>
                  <IoIosArrowDown className="below" />
                </div>
              </Grid>
            )}
          </Grid>
        </Grid>
      </Grid>
      <CardContent className="flex items-center p-6 m-2"></CardContent>
    </Card>
  );
}

export default PoolCard;
