import React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { isEmpty } from 'lodash';
const columns = [
  { id: 'user', label: 'USERNAME/ID' },
  { id: 'qty', label: 'QTY' },
  {
    id: 'price',
    label: 'PRICE',
    align: 'right'
  },
  {
    id: 'reward',
    label: 'REWAED',
    align: 'right'
  },
  {
    id: 'value',
    label: 'VALUE',
    align: 'right'
  }
];
export default function SingleTable({ single }) {
  return (
    <div sx={{ width: '100%', overflow: 'hidden' }}>
      <TableContainer
        sx={{
          maxHeight: 390,
          '&::-webkit-scrollbar': {
            width: 5,
            height: 5
          },
          '&::-webkit-scrollbar-track': {
            background: '#ECECEC'
          },
          '&::-webkit-scrollbar-thumb': {
            backgroundColor: '#7A6DE2'
          }
        }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}>
                  <div className="size-nine opacity-25 font-semibold">
                    {column.label}
                  </div>
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {!isEmpty(single) ? (
              single
                // .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((s) => {
                  return (
                    <TableRow hover tabIndex={-1}>
                      {columns.map((column) => {
                        const value = s[column.id];

                        return (
                          <TableCell
                            key={column.id}
                            align={column.align}
                            sx={{
                              borderBottom: 'none'
                            }}>
                            <text className="text-xs opacity-75">
                              {column.format && typeof value === 'number'
                                ? column.format(value)
                                : value}
                            </text>
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })
            ) : (
              <TableRow>
                <TableCell
                  colSpan={5}
                  sx={{
                    border: 'none'
                  }}>
                  <p className="flex justify-center text-center opacity-50 text-xs">
                    Sorry, No data available
                  </p>
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
