import React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { isEmpty } from 'lodash';
const columns = [
  { id: 'coin', label: 'ASSET' },
  {
    id: 'reward',
    label: 'TOTAL REWARD',
    align: 'right'
  }
];
export default function PoolTable({ single }) {
  return (
    <>
      <TableContainer
        sx={{
          maxHeight: 310
        }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                  sx={{
                    borderBottom: 'none'
                  }}>
                  <text className="opacity-25 size-nine font-semibold">
                    {column.label}
                  </text>
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {!isEmpty(single) ? (
              single.map((s) => {
                return (
                  <TableRow hover tabIndex={-1}>
                    {columns.map((column) => {
                      const value = s[column.id];

                      return (
                        <TableCell
                          key={column.id}
                          align={column.align}
                          sx={{
                            borderBottom: 'none'
                          }}>
                          <text className="text-xs opacity-75 ">
                            {column.format && typeof value === 'number'
                              ? column.format(value)
                              : value}
                          </text>
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })
            ) : (
              <TableRow>
                <TableCell
                  colSpan={2}
                  sx={{
                    border: 'none'
                  }}>
                  <p className="flex justify-center text-center opacity-50 text-xs">
                    Sorry, No data available
                  </p>
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
